import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
  useLocation,
} from "react-router-dom";
import LandingPage from "./LandingPage";
import MainPage from "./MainPage";
import CompanyPage from "./CompanyPage";
import MenuBar from "./MenuBar";
import TechnologyPage from "./TechnologyPage";
import ProductPage from "./ProductPage";
import CareerPage from "./CareerPage";
import ContactPage from "./ContactPage";
import NewsPage from "./NewsPage";

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <AnimatedRoutes />
    </Router>
  );
}

function AnimatedRoutes() {
  return (
    <div className="w-screen h-[100dvh] flex flex-col overflow-hidden">
      <div className="z-50 sticky top-0">
        <MenuBar />
      </div>
      <Routes>
        <Route path="/CES24" element={<LandingPage />} />
        <Route path="/main" element={<MainPage />} />
        <Route path="/about-us" element={<CompanyPage />} />
        <Route path="/about-us/news" element={<NewsPage />} />
        <Route path="/technology" element={<TechnologyPage />} />
        <Route path="/product" element={<ProductPage />} />
        <Route path="/career" element={<CareerPage />} />
        <Route path="/contact" element={<ContactPage />} />
        <Route path="/" element={<Navigate to="/CES24" replace />} />
      </Routes>
    </div>
  );
}


export default App;
