import {useEffect, React} from "react";

const Slide = ({name, content, parentsUpdater, snap}) => {
    useEffect(() => {
        if (parentsUpdater) {
            parentsUpdater(name); // 이름을 Presentation에 추가
        }
    }, [name, parentsUpdater]);

    return (<>
        <section name={name} className="flex h-screen" style={{
            scrollSnapAlign: snap?snap : "start",
        }}>
            {content&&content()}
        </section>
    </>);
};

export default Slide;
