import { useEffect, useState } from "react";
import Footer from "./Footer";
import ContactForm from "./ContactForm";
import Page from "./Page"

function RotatingItems() {
  const [messages, setMessages] = useState([
    "CPUs.",
    "GPUs.",
    "Servers.",
    "Datacenters.",
    "Automotives."
  ]);
  const [activeIndex, setActiveIndex] = useState(0);

  useEffect(() => {
    const interval = setTimeout(() => {
      setActiveIndex((prevIndex) => (prevIndex + 1) % messages.length);
    }, 2000);

    return () => clearTimeout(interval);
  }, [activeIndex, messages]);

  return (
    <div className="relative">
    {messages.map((message, index) => (
        <span
          key={index}
          className={`absolute transition-opacity duration-1000 ${
            index === activeIndex ? "opacity-100" : "opacity-0"
          }`}
        >
          {message}
        </span>
    ))}
    </div>
  );
}

function RotatingImages() {
  const [locations, setLocations] = useState([
    require("../assets/cover/cpu.jpg"),
    require("../assets/cover/gpu.jpg"),
    require("../assets/cover/server.jpg"),
    require("../assets/cover/datacenter.jpg"),
    require("../assets/cover/automotive.jpg"),
  ]);
  const [activeIndex, setActiveIndex] = useState(0);

  useEffect(() => {
    const interval = setTimeout(() => {
      setActiveIndex((prevIndex) => (prevIndex + 1) % locations.length);
    }, 2000);

    return () => clearTimeout(interval);
  }, [activeIndex, locations]);

  return (
    <>
      {locations &&
        locations.map((location, index) => (
          <img
            key={index}
            src={location}
            alt=""
            className={`absolute transition-opacity duration-1000 ${
              index === activeIndex ? "opacity-40" : "opacity-0"
            }`}
          />
        ))}
    </>
  );
}

function Content() {
  const title = "A Liquid-Cooling\nPowerhouse";
  const content =
    "KoolMicro stands for kingly cooling solutions for microelectronics. We are pioneers in creating cooling solutions heterogeneously integrated with advanced packages for AI, while redefining liquid cooling technology to empower the future of high-perfomance computing, integrated automotive systems, and data-intensive servers.";
}

function Cover() {
  return (
    <>
      <div className="flex relative w-full h-full items-center justify-center md:ps-[50vw] md:p-0 md:pe-[5rem] px-5">
        <div className="absolute w-full h-full top-0 left-0 bg-black overflow-hidden -z-50">
          <RotatingImages />
        </div>
        <div className="flex flex-col text-white font-pretendard md:text-4xl text-xl items-end">
          <img
            src={require("../assets/koolmicro2.png")}
            alt=""
            className="max-w-[30rem] w-full"
          />
          <div className="flex flex-col justify-end">
            <div className="w-full mt-10 mb-2">Liquid Cooling Solutions</div>
            <div className="flex w-full">
              <div className="flex text-white me-5">for</div>
              <div className="flex bg-brand-electric-blue rounded-md font-black text-black flex-grow">
                <RotatingItems />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

