import {useLocation} from 'react-router-dom';
import {useState} from 'react';
import {Link, useNavigate} from 'react-router-dom';
import logoImage from '../assets/kool-logo.png';

/**
 * DropdownMenu Component
 * @param {Object} props - props for component
 * @param {string} props.name - name for component
 * @param {list} props.items - list of sub components button
 * @param {isSelected} props.isSelected - set true to highlight the menu (default: false)
 * @param {string} [props.color] - font bg color for button (default: '#5ADBFF')
 * @param {string} [props.colorFont] - hover font color (default: '#303030')
 * @param {Function} [props.onClick] - click handler
 * @returns {JSX.Element} - DropdownMenu component
 */

const DropdownMenu = ({ name, items, isSelected=false, color="#5ADBFF", colorFont="#303030", onClick }) => {
    onClick = onClick ? onClick : ()=>{};

    return (<>
        <div className="font-pretendard group h-full relative">
            <button 
                className={"h-full font-pretendard px-5 font-medium group-hover:bg-gray-100 group-hover:text-["+colorFont+"] group-hover:bg-["+color+"] transform transition-all duration-300"}
                onClick={onClick}>
                {isSelected ?
                    <span className="underline underline-offset-8 decoration-[#5ADBFF] decoration-[3px]">
                        {name&&name}
                    </span> 
                    : 
                    <span>
                        {name&&name}
                    </span>
                }
            </button>

            {items&&
                <div className="absolute top-full group-hover:shadow-lg pointer-events-none opacity-0 w-full group-hover:pointer-events-auto group-hover:opacity-100 transform transition-all duration-300">
                    <ul>
                        {items.map((item, index) => (
                        <li
                            key={index}
                            className="text-center"
                        >
                            <DropdownItem name={item.name} location={item.location} items={item.children} />
                        </li>
                        ))}
                    </ul>
                </div>
            }
        </div>
    </>);
};
const DropdownItem = ({name, location, items}) => {
    const navigate = useNavigate();
    return (
      <>
        <div className="h-full relative p-0 group/item">
          <button
            className="w-full bg-brand-kool-blue hover:bg-brand-darker-blue text-gray-50 py-2 transition duration-300"
            onClick={() => {
              navigate(location);
            }}
          >
            {name}
          </button>
          {items && (
            <div className="absolute left-full top-0 pointer-events-none opacity-0 w-full group-hover/item:pointer-events-auto group-hover/item:opacity-100 transform transition-all duration-300">
              <ul className="p-0 m-0">
                {items.map((item, index) => (
                  <li key={index} className="text-center">
                    <button
                      className="w-full bg-brand-silicon-white hover:bg-brand-electric-blue font-medium text-black py-2 transition duration-300"
                      onClick={() => {
                        navigate(item.location);
                      }}
                    >
                      {item.name}
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>
      </>
    );
}

const ToLandingPageButton = () => {
    return(<>
        <Link to="/CES24" className="flex rounded-full h-[fit-content] text-black hover:text-white hover:bg-black bg-brand-electric-blue px-5 py-1">
            <span className="font-pretendard">CES 2024</span>
        </Link>
    </>);
}

const MenuButton = ({ name, items, isSelected=false, onClick }) => {
    return(<>
        {isSelected?
            <button className="w-full py-4 ps-5 text-left font-pretendard" onClick={onClick}>
                {name}
            </button>
            :
            <button className="w-full py-4 ps-5 text-left font-pretendard" onClick={onClick}>
                {name}
            </button>
        }
    </>);
}

const MenuBar = () => {
    const navigate = useNavigate();
    let location = useLocation();
    let [isSliding, setIsSliding] = useState(false);
    let [isSlideIn, setIsSlideIn] = useState(false);

    const toggleMenu = () => {
        if(!isSliding) {
            setIsSliding(true);
            if(isSlideIn) {
                setIsSlideIn(false);
            } else {
                setIsSlideIn(true);
            }
            setTimeout(()=>{
                setIsSliding(false);
            },200);
        }
    }
    
    class ItemInfo {
      /**
       * Represents an item with a name, location, and an optional child item.
       * @param {string} name - The name of the item
       * @param {string} location - The location of the item
       * @param {ItemInfo} [children] - Optional child item
       */
      constructor(name, location, children = undefined) {
        this.name = name;
        this.location = location;
        this.children = children;
      }
    }

    const pageInfos = [
      new ItemInfo("Main", "/main"),
      new ItemInfo("About Us", "/about-us", [
        new ItemInfo("Newsroom", "/about-us/news"),
      ]),
      new ItemInfo("Technology", "/technology", [
        new ItemInfo("Operations", "/technology/operation"),
      ]),
      new ItemInfo("Product", "/product", [
        new ItemInfo("IMMC", "/product/immc-1", [
          new ItemInfo("IMMC-1", "/product/immc-1"),
        ]),
      ]),
      new ItemInfo("Career", "/career"),
      new ItemInfo("Contact", "/contact"),
    ];
    
    return (
      <>
        <nav className="md:flex hidden flex-row shadow bg-white">
          <div className="ms-5 me-5 my-5 ">
            <img
              src={logoImage}
              alt=""
              className="h-[2rem] w-auto object-contain py-0"
            />
          </div>
          <div className="flex items-center">
            <ToLandingPageButton />
          </div>

          <div className="flex ms-auto">
            <div className="md:flex hidden items-center">
              {pageInfos.map((info, index) => (
                <DropdownMenu
                  name={info.name}
                  key={info.location}
                  onClick={() => {
                    navigate(info.location);
                  }}
                  items={info.children}
                  isSelected={
                    info.location.split("/")[1] ===
                    location.pathname.split("/")[1]
                  }
                />
              ))}
            </div>
          </div>
        </nav>

        {/* 모바일 메뉴 */}
        <nav className="md:hidden flex flex-row h-[3rem] bg-white relative">
          <button
            className="flex flex-col px-3 py-4 rounded-xl"
            onClick={() => {
              toggleMenu();
            }}
          >
            <div className="rounded-full w-6 h-1 bg-gray-500 mb-1"></div>
            <div className="rounded-full w-6 h-1 bg-gray-500 mb-1"></div>
            <div className="rounded-full w-6 h-1 bg-gray-500"></div>
          </button>
          <div className="flex items-center">
            <ToLandingPageButton />
          </div>
          <div className="flex ms-auto h-full items-center me-3">
            <div className="font-pretendard text-xl py-2 px-3 font-black bg-[#004385] rounded-lg">
              <img
                alt="koolmicro"
                src={require("../assets/koolmicro.png")}
                className="h-5 w-auto object-contain"
              />
            </div>
          </div>
          <div
            className={
              "absolute top-0 mt-[3rem] shadow-md h-[100dvh] " +
              (isSlideIn ? " flex " : isSliding ? " flex " : " hidden ") +
              (isSliding
                ? isSlideIn
                  ? " animate-slide-in "
                  : " animate-slide-out "
                : "")
            }
          >
            <ul className="bg-gray-100 h-full max-w-[20rem]">
              {pageInfos.map((info, index) => (
                <MenuButton
                  name={info.name}
                  key={info.location}
                  onClick={() => {
                    navigate(info.location);
                    toggleMenu();
                  }}
                  items={info.children}
                  isSelected={info.location === location.pathname}
                />
              ))}
            </ul>
          </div>
        </nav>
      </>
    );
};

export default MenuBar;