const FloatingHomeButton = () => {
    let [hide, setHide] = useState(true);

    useEffect(()=>{
        const targetDiv = document.querySelector('[name="contact"]');
        const observer = new IntersectionObserver(
          ([entry]) => {
            // 뷰포트와 isIntersecting이 true면 지움
            setHide(entry.isIntersecting);
          },
          { root: null, threshold: 0.1 } // threshold: 10% 겹칠 때 감지
        );

        if (targetDiv) observer.observe(targetDiv);

        return () => {
        if (targetDiv) observer.unobserve(targetDiv);
        };
    },[]);

    if(hide)
        return <></>
    
    return(
    <>
        <div className="fixed top-10 right-10">
            <Link className="flex p-0 border-none h-[3rem]" to="/main">
                <img 
                    src={homeImage} 
                    alt="" 
                    className="h-full w-auto object-contain
                    transition-transform duration-200 ease-out hover:scale-110"
                />
            </Link>
        </div>
    </>);
};

