import React, { useState, useEffect } from "react";

const Presentation = ({ children, id, initialSectionName }) => {
    const [currentSection, setCurrentSection] = useState();
    const [nameList, setNameList] = useState([]);
    
    const addNameToList = (name) => {
        setNameList((prevList) => {
            if (!prevList.includes(name)) {
            return [...prevList, name];
            }
            return prevList;
        });
    };
    
    // hashurl을 고려해서 hash 세팅
    const setHash = (hash) => {
      setCurrentSection(hash);

      // 해쉬를 받아오기
      if(window.location.hash.startsWith("#/")) {
        hash = window.location.hash.split('#')[1] + '#' + hash;
      }
      window.history.replaceState(null, "", `#${hash}`);
    };

    // 전체 리렌더링 될 때 location 확인해서 해당 위치로 스크롤
    useEffect(() => {
        const hash = window.location.hash; // 현재 URL의 해시 값 가져오기 (예: #section1)
        if (hash) {
            const targetElement = hash
              ? document.querySelector(`[name="${hash.slice(1)}"]`)
              : null;
            if (targetElement) {
                // 애니메이션 없이 바로 스크롤
                targetElement.scrollIntoView();
            }
        }
    }, []);

    // 현재 뷰포트에 보이는 section id 추적해 url에 업데이트
    useEffect(() => {
         const container = document.getElementById(id);

        if (!container) {
            console.error("Container not found with ID:", id);
            return;
        }

        const sections = container.querySelectorAll("section");

        const handleScroll = () => {
            const containerRect = container.getBoundingClientRect();

            sections.forEach((section) => {
                const sectionRect = section.getBoundingClientRect();
                const sectionName = section.getAttribute("name");

                // Check if section is centered in the viewport
                if (
                    sectionRect.top >= containerRect.top &&
                    sectionRect.bottom <= containerRect.bottom
                ) {
                    if (currentSection !== sectionName) {
                        setHash(sectionName); // Update hash
                    }
                }
            });
        };

        container.addEventListener("scroll", handleScroll);

        return () => container.removeEventListener("scroll", handleScroll);
    }, [currentSection, id]);

    const enhancedChildren = React.Children.map(children, (child) => {
        if (React.isValidElement(child)) {
            return React.cloneElement(child, { parentsUpdater:addNameToList });
        }
        return child;
    });

    return (
      <>
        <div
          className="flex flex-col h-screen"
          id={id}
          style={{
            overflowY: "scroll",
            overflowX: "hidden",
            scrollSnapType: "y mandatory",
          }}
        >
          {enhancedChildren}
        </div>
      </>
    );
};

export default Presentation;